(function() {
    'use strict';

    var app = angular.module('app');

    app.directive("antiForgeryToken", function () {
        return {
            restrict: 'E',
            replace: true,
            scope: {
                token: "="
            },
            template: "<input type='hidden' name='{{token.name}}' value='{{token.value}}'>"
        };
    });

    app.directive('idsrvTranslateScope', ['$compile', '$translate', function ($compile, $translate) {
        return {
            restrict: 'A',
            scope: {
                idsrvTranslateScope: '='
            },
            priority: 0,
            link: function (scope, element, attrs) {
                var idsrvScope = scope.idsrvTranslateScope;
                var transKey = 'scope_dn_' + idsrvScope.name;

                //scope.originalText = originalText;

                var translationChangeOccurred = function () {
                    var result = $translate.instant(transKey);
                    if (result) {
                        element.html(result);
                    } else {
                        element.html(idsrvScope.displayName);
                    }

                    $compile(element.contents())(scope);
                };

                //translation changes by default while linking!
                translationChangeOccurred();

                scope.$on('$translateChangeEnd', translationChangeOccurred);
            }
        };
    }]);

})();